import React from 'react';
import './css/SnackMonth.css';
const captions = require('./captions.json');
const dates = require('./dates.json');

class SnackMonth extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            images : []
        }
    }
    
    componentDidMount = () => {
        let images = [...this.state.images];
        for(let i=1; i<=31; i++) {
            try {
                let path = `./snack-pics/${this.props.year}/${this.props.month}/${i}.jpg`;
                let img = (
                    <div key={path} className="hvrbox">
                        <img src={require(`${path}`)} alt="" className="hvrbox-layer_bottom" />
                        <div className="hvrbox-layer_top">
                            <p className="hvrbox-text"><b>{dates[path]}</b> <br/> {captions[path]}</p>
                        </div>
                    </div>
                );
                images.push(img);
            } catch (err) {
            	let path = `./snack-pics/${this.props.year}/${this.props.month}/${i}.jpg`;
            	console.log(path);
                break;
            }
        }
        this.setState({ images });
    }

    render() {  
        let images = this.state.images.map(img => {
            return img;
        });

        if(this.state.images.length !== 0) {
            return (
                <div className='snacks'>
                    <h1>{this.props.month} {this.props.year}</h1>
                    { images }
                </div>
            );
        } else {
            return null;
        }
    }
}

export default SnackMonth;